import React, { useState, useEffect } from 'react';
import Product from '../Product/Product';
import "./Shop.css"


const Shop = () => {

    const [state, setstate] = useState([]);
    const [cart, setcart] = useState([]);

    useEffect(() => {
        {
            fetch('Data.JSON')
                .then(res => res.json())
                .then(data => setstate(data))
        }
    }, [])

const handleAddToCart = (state) => {
    // console.log(state.name);
    const newCart = [...cart, state];
    setcart(newCart);
}

let total = 0;
for(const product of cart){
    total=parseInt(product.CourseFee)+total;
}

    return (
        <div className="shop-container d-flex">
            {/* <h1>{state.length}</h1> */}

            <div className="people-container flex-grow-1 my-3 mx-3">
                {
                    state.map(x => <Product 
                        key={x.key}
                        img={x.Image} 
                        name={x.courseName} 
                        CourseFee={x.CourseFee}
                        handleAddToCart={handleAddToCart}
                        
                        ></Product>)
                }
            </div>

            <div className="cart-container">
                <h2>Order Summary</h2>

                <h3>Item Ordered: {cart.length}</h3>
                <h5>Total Price: ${total} </h5>

            </div>



        </div>
    );
};

export default Shop;
