import React from 'react';
import * as ReactBootstrap from "react-bootstrap"
import { FaBeer } from 'react-icons/fa';

const Product = (props) => {
    const { key, img, name, CourseFee } = props;
    return (
        <div className="grid">
            <ReactBootstrap.Card style={{ width: '18rem' }}>
                <ReactBootstrap.Card.Img variant="top" src={img} />
                <ReactBootstrap.Card.Body>
                    <ReactBootstrap.Card.Title>{name}</ReactBootstrap.Card.Title>
                    <ReactBootstrap.Card.Text>
                     {CourseFee}
                    </ReactBootstrap.Card.Text>
                    <ReactBootstrap.Button variant="warning" onClick={()=>props.handleAddToCart(props)}> <FaBeer /> Add to Cart</ReactBootstrap.Button>
                </ReactBootstrap.Card.Body>
            </ReactBootstrap.Card>

        </div>
    );
};

export default Product;